@font-face {
  font-family:'San Francisco Pro Display';
  src: url(./assets/font/SFProDisplay-Light.woff2) format(woff2); 
  font-weight: 400;
  font-style: normal;
  font-display: swap;

}

@font-face {
font-family:'San Francisco Pro Display';
src: url(./assets/font/SFProDisplay-Bold.woff2) format(woff2); 
font-weight: 700;
font-style: normal;
font-display: swap;

}

@font-face {
font-family:'San Francisco Pro Text';
src: url(./assets/font/SFProText-Light.woff2) format(woff2); 
font-weight: 700;
font-style: normal;
font-display: swap;

}

@font-face {
font-family:'San Francisco Pro Text';
src: url(./assets/font/SFProText-Bold.woff2) format(woff2); 
font-weight: 400;
font-style: normal;
font-display: swap;

}

@font-face {
font-family:'San Francisco Pro Display';
src: url(./assets/font/SFProDisplay-Medium.woff2) format(woff2); 
font-weight: 500;
font-style: normal;
font-display: swap;

}

.App {
 
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

