/* Основные стили для выпадающего меню */
.dropdown--menu {
  position: absolute;
  background-color: #ffffff; /* Белый фон */
  box-shadow: 0px 4px 8px;
  padding: 10px;
  list-style: none;
  top: 50px; /* Смещение вниз */
  left: 0;
  width: 150px;
  display: none; /* По умолчанию скрыто */
  z-index: 11;
}

/* Показываем меню, если state dropdownMenu === true */
.dropdown--menu.active {
  display: block;
}

/* Стили для элементов меню */
.dropdown--Item {
  padding: 10px;
  cursor: pointer;
  transition: background 0.3s ease;
  text-align: start;
  font-weight: 400;
  font-size: 20px;
  color: black;
}

/* Ховер эффект */
.dropdown--Item:hover {
  background-color: var(--color-button-hover); /* Голубой фон при наведении */
  color: #ffffff; /* Белый текст */
}

